<template>
  <el-table :data="games">
    <el-table-column type="name">
      <template slot-scope="props">
        <p>Name: {{ props.row.name }}</p>
      </template>
    </el-table-column>

    <el-table-column type="index" />
    <el-table-column label="ID" prop="id" />
    <el-table-column label="" prop="name" />

    <el-table-column label="" prop="parentType" />
    <el-table-column label="" prop="type" />
    <el-table-column label="" prop="startTime" />
    <el-table-column label="" prop="endTime" />
    <el-table-column label="" prop="intervalTime" />
    <el-table-column label="" prop="url" />
    <el-table-column label="" prop="children" />
  </el-table>
</template>

<script>
export default {
  data () {
    return {
      games: []
    }
  },
  created () {
    this.fetchGamesList()
  },
  methods: {
    // 
    fetchGamesList () {
      this.$axios.get('/api-g/GameTypeConfig/tree').then(response => {
        this.games = response.data.data
      }).catch(error => console.log(error))
    }
  }
}
</script>
